.list {
    width: 450px;
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
    border: 1px solid #2a363b;
    border-radius: 4px;
    margin-bottom: 5px;
}

.info {
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    margin: 5px 0px;
}

button {
    background-color: #e9e9e9;
    padding: 2px 10px;
    margin-top: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 15px;
    color: #2a363b;;
    border-radius: 15px;
    width: auto;
}

button:hover {
    cursor: pointer;
}

button:active {
    background: #d3d3d3;
}

